@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background: linear-gradient(92.23deg, #FF9130 21.43%, /* #ff9222 */    /* #ffc284 */    /* #FF6000 */    #FF5B22 50.63%,    #243763 100%,   /* #293f72 100%, */   /* #5f7ec5 */   /* #7c95cf */   /* #c4cfea */    #141f38 117.04%  );
  box-shadow: 0px 4.42184px 107.23px hsl(222, 47%, 57% , 50%);
  transition:all 0.3s ease ;
  -webkit-transition:all 0.3s ease ;
  -moz-transition:all 0.3s ease ;
  -ms-transition:all 0.3s ease ;
  -o-transition:all 0.3s ease ;
}

.btn:hover {

  background:
   linear-gradient( 
      92.23deg,  
      #141f38 21.43%,  
      #293f72 50.63%,  
      #FF5B22 100%,   
      #FF9130 117.04%  );

  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}

.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #141f38 21.43%,
    #293f72 50.63%,
    #FF5B22 100%,
    #FF9130 117.04%
  )
}

.active {
  color: #7c95cf;
  padding: 1rem;
  border-radius: 100%;
}


@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-[#c4cfea];
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-[#c4cfea] font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#293f72] to-[#FF9130] font-bold font-primary hover:from-[#FF9130] hover:to-[#141f38];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
